<template>
  <div>
    <title-bar :title-stack="titleStack" />
    <section class="section is-main-section">
      <b-tabs type="is-boxed" size="is-medium">
        <card-component title="个人设置" icon="cogs">
          <div class="columns">
            <div class="column is-1">
              <figure class="image is-128x128">
                <img :src="headfile" />
              </figure>
            </div>
            <div class="column is-2">
              <div class="mt-4">
                <b-field horizontal label="账号:">
                  <b-input v-model="userinfo.uname" type="text" placeholder="账号" disabled />
                </b-field>
                <b-field horizontal label="昵称:">
                  <b-input v-model="userinfo.unickname" type="text" placeholder="昵称" />
                </b-field>
              </div>
            </div>
            <div class="column is-2">
              <div class="mt-4">
                <b-field horizontal label="密码:">
                  <b-input v-model="userinfo.upsd" type="text" placeholder="密码" />
                </b-field>
                <b-field horizontal label="邮箱:">
                  <b-input v-model="userinfo.uemail" type="text" placeholder="邮箱" />
                </b-field>
              </div>
            </div>
            <div class="column is-2">
              <div class="mt-4">
                <b-field label="选择第一个默认项目" label-position="on-border">
                  <b-select v-model="selectedproject[0]" placeholder="选择项目" expanded>
                    <option v-for="(item, index) in allprojects" :key="index" :value="item">{{ item.name }}</option>
                  </b-select>
                </b-field>
                <b-field label="选择第二个默认项目" label-position="on-border">
                  <b-select v-model="selectedproject[1]" placeholder="选择项目" expanded>
                    <option v-for="(item, index) in allprojects" :key="index" :value="item">{{ item.name }}</option>
                  </b-select>
                </b-field>
                <div class="level">
                  <div class="level is-left" />
                  <div class="level is-right">
                    <b-button size="is-small" type="is-cgreen-2" @click="uploadNew()">保存</b-button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </card-component>
      </b-tabs>
    </section>
  </div>
</template>

<script>
import headimg from '../../../public/img/s.png'
import TitleBar from '@/components/TitleBar'
import CardComponent from '@/components/Card/CardComponent'
import { allprojects } from '../../scripts/common'
import { postFunc } from '@/scripts/internal/httpFunc'
export default {
  /**
   * 个人设置
   * 个人设置页面
   */
  name: 'Settings',
  components: {
    TitleBar,
    CardComponent
  },
  data() {
    return {
      /**
       * 用户信息
       */
      userinfo: {},
      headfile: '', // 头像图片
      /**
       * 所有后端的项目
       */
      allprojects: [],
      /**
       * 选择的两个项目对象
       */
      selectedproject: []
    }
  },
  computed: {
    titleStack() {
      return ['RoWalock', '个人设置']
    }
  },
  mounted() {
    this.init()
  },

  methods: {
    /**
     * 初始化
     */
    init() {
      // 更新从远程获取的啥项目
      allprojects()
        .then(result => {
          this.allprojects = result
        })
        .catch(error => {
          this.allprojects = error
        })
        .finally(() => {
          this.headfile = headimg
          this.userinfo = this.$store.state.common.activeusers
        })
    },

    /**
     *提交新的
     */
    uploadNew() {
      const postdata = {
        userinfo: JSON.stringify(this.userinfo),
        selectedproject: JSON.stringify(this.selectedproject)
      }
      console.log(postdata)
      postFunc('/profile/setting/updateuser', postdata)
        .then(result => {
          // console.log(result)
          if (result.resstatus === true) {
            this.$store.commit('uploadDefaultTwoProjs', this.selectedproject) // 提交
            this.$store.commit('updateUserInfo', result) // 提交更新
            this.$buefy.toast.open({
              duration: 3000,
              message: result.meesage,
              type: 'is-success'
            })
            this.selectedproject = [] // 初始化
          } else {
            this.$buefy.toast.open({
              duration: 3000,
              message: '个人信息更新失败!请重试或检查信息',
              type: 'is-warning'
            })
          }
        })
        .catch(() => {
          this.$buefy.toast.open({
            duration: 3000,
            message: '个人信息更新失败!请检查填入信息和选择项目是否存在!',
            type: 'is-cred-2'
          })
        })
    }
  }
}
</script>

<style scoped>
.margin-0 {
  margin: 0rem;
}
</style>
